.Drawer__Drawer--2D_CT{
	position: fixed;
	z-index: 90;
	background: #fff;
	left: 0;
	top: 0;
	bottom: 0;
	width: 80%;
	max-width: 300px;
	-webkit-transform: translateX(0px);
	        transform: translateX(0px);
	-webkit-transition: -webkit-transform .5s ease;
	transition: -webkit-transform .5s ease;
	transition: transform .5s ease;
	transition: transform .5s ease, -webkit-transform .5s ease
}
.Drawer__close--2gKqT{
	-webkit-transform: translateX(-300px);
	        transform: translateX(-300px);
}
.Drawer__Drawer--2D_CT ul{
	margin: 0;
	padding: 0;
}
.Drawer__Drawer--2D_CT ul li{
	list-style: none;
}
.Drawer__Drawer--2D_CT ul li a{
	color: #000;
	font-size: 25px;
	position: relative;
	text-align: center;
	display: inline-block;
	margin-left: 10px;
	font-weight: bold;
	margin-bottom: 10px;
	cursor: pointer;
	text-decoration: none;
}